.services {
  .article {
    display: flex;
    align-self: center;
    flex-direction: column;
    border-radius: 0.4rem;
    box-shadow: -0.2rem 0.2rem 0.3rem #00000066;
    padding: 2rem;
    margin: 1rem;
    transition: transform 0.3s ease;

    &.recruitment,
    &.job-in-europe,
    &.rent-bed {
      .image {
        margin-top: -4rem;
        height: 13rem;

        @media only screen and (orientation: landscape) {
          height: 25vw;
        }
      }
    }

    &.recruitment {
      background: linear-gradient(45deg, #ab5850b9, #d2b365d9, transparent);
    }

    &.job-in-europe {
      background: linear-gradient(45deg, #6988ba, #a1b9ded9, transparent);
    }

    &.rent-bed {
      background: linear-gradient(45deg, #998c76, #c8baa7d9, transparent);
    }

    &.trading {
      background: linear-gradient(to top, transparent, #a1b9ded9, transparent);

      .title {
        padding: 0.2rem;
        background: #223047;
        border: double 0.3rem #dde0da;
        margin-top: 11.5rem;
        align-self: center;
        position: absolute;
        color: #ffffff;
      }

      .image {
        align-self: center;
        position: relative;
      }

      .sub-title {
        text-align: center;
        letter-spacing: 0.1rem;
      }

      .product-title {
        text-transform: uppercase;
        font-weight: bold;
        color: #fffefe; 
        border: double 0.3rem #dde0da;
        background: #223047;
        padding: 0.3rem;
      }
      

      .ending {
        color: #0a0a0a; 
        border: double 0.3rem #223047;
        padding: 0.5rem;
        text-align: center;
      }
    }

    .title {
      display: flex;
      flex-direction: column;
      color: #443665;
      letter-spacing: 0.2rem;
      font-weight: bold;
      margin-bottom: 1rem;
      width: max-content;
      text-align: center;
    }

    .image {
      position: absolute;
      height: 15rem;
      right: 1vw;
      z-index: -1;

      @media only screen and (orientation: landscape) {
        height: 47vw;
      }
    }

    .text {
      margin-block-start: 0;
      color: #0a0a0a;
      line-height: 1.6;
    }
  }
}
