footer {
  background: #bbbcbe;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  position: relative;
  margin-top: 0.8rem;

  .social {
    position: absolute;
    margin-top: 0.2rem;
    right: 2vw;
    font-size: 2rem;
    color: #1c1cf1;
    z-index: 1;

    &:hover {
      color: #0a0a97;
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    position: relative;
    color: #222222;
    text-align: center;

    .title {
      letter-spacing: 0.1rem;
      color: #e34f0b;
      text-shadow: 0 0.1rem 0.1rem #a5c2c9;
    }

    .address {
      text-align: center;
    }
  }

  .copyright {
    color: #222222;
    text-align: center;
    font-weight: bold;

    .link {
      color: #222222;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}
