.contact {
  .article {
    display: flex;
    flex-direction: column;
    position: relative;

    .title {
      background: #f3bf7a;
      color: #333;
      text-align: center;
      border: solid 0.2rem;
      border-color: #c0c0c0 #696969 #808080 #c0c0c0;
      border-radius: 0.5rem;
      box-shadow: 0.1rem 0.1rem 0.2rem #a5a2a2b4;
      width: 70vw;
      padding: 2vw;
      align-self: center;
    }

    .container {
      display: flex;
      align-items: flex-start;
      flex-direction: column;

      @media only screen and (orientation: landscape) {
        align-items: flex-end;
        flex-direction: row;
        justify-content: center;
      }

      .text {
        padding: 4vw;
        background: #b8c5c4;
        box-shadow: 0.1rem 0.1rem 0.5rem #757086ef;
        border-radius: 0.4rem;
        border-top-right-radius: 0;
        position: relative;
        color: #0a0a0a;
        margin-block-end: 0;

        @media only screen and (orientation: landscape) {
          height: 22.5vw;
          width: 45vw;
        }
      }

      .image {
        filter: drop-shadow(0.1rem 0.1rem 0.2rem #616163ef);
        position: relative;
        height: 45vw;
        left: 0;
        margin-bottom: 1rem;
        border-top-right-radius: 1vw;
        z-index: -1;
      }
    }
  }

  .form-background {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 5vw;
    box-shadow: 0.1rem 0.01rem 0.1rem 1rem #76808d;
    background: radial-gradient(circle, #f9fac3, #b8c5c4);
    border: 2vw solid white;
    border-radius: 0.9vw;
    width: 83vw;

    @media only screen and (orientation: landscape) {
      margin: 5vw 0;
    }

    .contact-form {
      display: flex;
      align-items: center;
      text-align: center;
      flex-direction: column;
      color: #0a0a0a;
      width: 90vw;

      .form {
        .field {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: space-between;
          width: 60vw;
          color: #0c0c46;

          @media only screen and (orientation: landscape) {
            flex-direction: row;
            align-items: center;
          }

          &.agreement {
            display: block;
            margin-top: 3vw;
            align-items: flex-start;
            width: fit-content;

            .required {
              color: red;
            }

            .rodo {
              color: #0404db;
              background: none;
              border: none;
              text-decoration: underline;
            }
          }
        }

        .input {
          margin: 1vw 0;
          padding: 1vw;
          width: 53vw;
          background-color: #ffc964;
          border: 0.1rem solid #feffff;
          box-shadow: 0.01rem 0.05rem 0.1rem 0.1rem #383733bb;
          border-radius: 0.3rem;
          align-self: end;

          &.message {
            height: 5rem;
            overflow: auto;
            resize: none;
          }
        }

        .send {
          margin: 3vw;
          position: relative;
          width: 30vw;
          padding: 0.9vw;
          cursor: pointer;
          border: none;
          background: #76808d75;
          color: #7e7a7a;
          border-radius: 1vw;

          &:hover {
            background-color: #363450;
            color: #ffffff;
            filter: drop-shadow(0 0.1rem 0.1rem #363636);
          }
        }
      }
    }
  }

  .modal {
    z-index: 10000000;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 9.5rem;
    position: fixed;
    width: 90vw;
    height: 80vh;
    background: linear-gradient(to bottom, #a8ccff, #8294a7);

    .close {
      background: red;
      right: 0;
      position: absolute;
    }

    .policy {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 2vw;
      width: 80%;
      height: 80%;
      background: white;
      border-radius: 2vw;
      overflow-y: auto;
    }
  }
}
