body {
  font-family: Merriweather;
  margin: 0;
  padding: 0;

  main {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 1;
  }

  h1,
  a {
    font-family: kanit;
  }

  h1 {
    font-size: 1rem;

    @media only screen and (orientation: landscape) {
      font-size: 1.8vw;
    }
  }

  h2,
  label {
    font-family: oswald;
  }

  h2 {
    font-size: 0.9rem;
  }

  p,
  ul li {
    font-family: cabin;
    font-weight: bold;
    font-size: 0.7rem;

    @media only screen and (orientation: landscape) {
      font-size: 1.4vw;
    }
  }

  button {
    cursor: pointer;

    @media only screen and (orientation: landscape) {
      font-size: 1.4vw;
      padding: 1vw;
    }
  }

  a {
    text-decoration: none;
    font-size: 0.7rem;
  }
}

.home {
  .welcome {
    .one,
    .two {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
    }

    .one {
      margin-top: -1rem;

      .title {
        position: relative;
        top: 3rem;
        right: 10vw;
        color: #0c0c46;
        z-index: 1;
      }

      .image {
        position: relative;
        height: 10rem;

        @media only screen and (orientation: landscape) {
          height: 30vw;
        }
      }
    }

    .two {
      margin: 1rem 2vw;
      padding: 1rem 10vw;
      background: linear-gradient(to right, #99b8e2, #e1e2e4, #99b8e2);
      clip-path: polygon(
        50% 0,
        90% 0,
        100% 50%,
        90% 100%,
        10% 100%,
        0 50%,
        10% 0
      );

      .text {
        filter: drop-shadow(0.01rem 0.01rem 0.06rem #f6f6cf);
        color: #0c0c46;
        text-align: center;
        margin: 1rem 0;
      }

      .image {
        box-shadow: 0.1rem 0rem 0.6rem black;
        height: 16rem;

        @media only screen and (orientation: landscape) {
          height: 50vw;
        }
      }
    }
  }

  .article {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    margin: 1rem 2vw;

    .text {
      position: relative;
      text-align: end;
      color: #0c0c46;
      border-top: solid 0.3rem;
      border-bottom: solid 0.3rem;
      border-color: #fc9337;
      padding: 2rem 2vw 2rem 10vw;
      width: 55vw;
    }

    .image {
      position: relative;
      height: 9rem;
      filter: drop-shadow(0rem 0.1rem 0.2rem grey);
      left: 5vw;
      transform: rotate(-40deg);
    }
  }

  .slide-show {
    width: 90vw;

    .slide,
    .button {
      border-radius: 2rem;
    }

    .slide {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 2rem;
      background: #969fa7cc;
      box-shadow: 0.5rem -0.5rem 1rem white;

      @media only screen and (orientation: landscape) {
        flex-direction: row;
        width: 80vw;
      }

      .image {
        box-shadow: 0rem 0.1rem 0.2rem 0.8rem #ffffff80;
        background-color: #292121c4;
        height: 10rem;
        margin: 2rem 4vw;
        border-radius: 0.5rem;

        @media only screen and (orientation: landscape) {
          height: 25vw;
        }
      }

      .description {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 80vw;
        padding: 0.6rem;

        .title {
          text-transform: uppercase;
          border-top: solid 0.1rem #0c0c46;
        }

        .text {
          color: #0c0c46;
          border-left: solid 0.1rem #0c0c46;
          padding: 0.5rem;
        }
      }
    }

    .button {
      position: relative;
      display: flex;
      align-items: center;
      font-weight: bold;
      background: #969fa7cc;
      border: none;
      margin: 0.5rem 0 0.5rem 60vw;
      box-shadow: 0 0.1rem 0.1rem #817e7e;

      .icon {
        font-size: 1.5rem;
      }
    }
  }

  .simple-link {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    width: 90vw;
    height: 12rem;
    margin-top: 2rem;
    border: solid 0.3rem;
    background: linear-gradient(-45deg, #736253, #d6c8c1, #edb302);
    box-shadow: inset 0 0 0.5rem #6e0404;

    @media only screen and (orientation: landscape) {
      height: 40vw;
    }

    .title,
    .link {
      filter: drop-shadow(0 0 0.2rem black);
      position: relative;
      color: whitesmoke;
    }

    .title {
      margin-block-start: 0;
      margin-block-end: 0;
      padding: 0.5rem 3vw;
      font-size: 1.4rem;
      text-decoration: underline;

      @media only screen and (orientation: landscape) {
        font-size: 3vw;
      }
    }

    .link {
      font-size: 1.5rem;
      padding-right: 5vw;

      @media only screen and (orientation: landscape) {
        font-size: 2.5vw;
      }

      &:hover {
        animation: scale 0.8s ease infinite;

        @keyframes scale {
          0% {
            transform: scale(1);
          }
          50% {
            transform: scale(1.2);
            filter: drop-shadow(0 0 0.5rem black);
          }
          100% {
            transform: scale(1);
          }
        }
      }
    }

    .image {
      position: absolute;
      left: 0;
      margin-top: 1rem;
      height: 13rem;
      filter: drop-shadow(0 0 0.2rem black);

      @media only screen and (orientation: landscape) {
        height: 42vw;
        margin-top: 2vw;
      }
    }
  }
}
