.about {
  .article {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    .title {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      position: relative;
      text-align: center;
      background: #f3bf7a;
      width: 80vw;
      border-radius: 0.5rem;
      filter: drop-shadow(0 0.1rem 0.1rem #a3a5a5);
      color: #161616;
    }

    .image {
      filter: drop-shadow(0.01rem 0.01rem 0.1rem #e4dfdf);
      width: 15vw;
    }

    .text {
      border-radius: 0.4rem;
      color: #0a0a0a;
      box-shadow: 0rem 0rem 0.1rem 0.2rem #fffefc;
      width: 70vw;
      padding: 1rem;
      text-align: end;
    }
  }

  .info {
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: 2rem;
    width: 90vw;
    margin-block-start: 0;
    margin-block-end: 0;
    background: linear-gradient(to right, #e7f9ff, #dce9ed, #e7f9ff);
    box-shadow: 0 0.1rem 0.1rem #a3a5a5;

    .title {
      display: flex;
      flex-direction: row;
      align-items: flex-end;
      justify-content: center;
      color: #fed549;
      text-shadow: 0 0 0.2rem #161616;
      text-transform: uppercase;
      font-size: 1.1rem;

      @media only screen and (orientation: landscape) {
        font-size: 2.3vw;
      }

      .image {
        filter: drop-shadow(0.01rem 0.01rem 0.1rem #dedfd5);
        height: 19vw;
        right: 1vw;
        top: -1.5vw;
      }
    }

    .container {
      .sub-title {
        color: #353434;
        margin-left: 3vw;
        margin-top: 2rem;
        margin-block-end: 0;
      }

      .text {
        border-radius: 0.4rem;
        color: #0a0a0a;
        background: #ffffff;
        border: solid 0.14rem #6979af;
        box-shadow: 0rem 0rem 0.1rem 0.3rem #fffefc;
        padding: 1rem;
        margin-bottom: 2rem;
      }
    }
  }
}
