.work {
  margin: 1rem;
  box-shadow: inset 0 0 1rem black;
  border-radius: 1rem;

  .job-offer {
    background: linear-gradient(45deg, #d8f8f7, #eef5f4, #b3e2e0);
    box-shadow: 0 4px 8px #0000001a;
    border-radius: 10px;
    padding: 1rem;
    margin: 2rem;
    box-shadow: 0 0 0.3rem #000000;
    width: 80%;

    .title {
      display: flex;
      flex-direction: row;
      align-items: center;
      color: #222222;
      margin-bottom: 0.5rem;
      height: 30vw;
      width: 70%;

      .arrow {
        margin-right: 0.5rem;
        color: #333333;
      }

      .image {
        position: absolute;
        right: 0;
        height: auto;
        width: 30vw;
        box-shadow: -0.2rem 0 0.3rem #000000;
      }
    }

    .description {
      color: #555555;
      margin-bottom: 1rem;
    }

    .list {
      list-style: disc inside;
      padding-left: 0;
    }

    .item {
      color: #555555;

      .salary,
      .accomodation,
      .time,
      .location {
        font-size: 1.5rem;
        margin-right: 0.3rem;

        @media only screen and (orientation: landscape) {
          font-size: 1.6rem;
        }
      }

      .salary {
        color: #4d8d4d;
      }

      .accomodation {
        color: #392118;
      }

      .time {
        color: #4242d4;
      }

      .location {
        color: #d73838;
      }
    }
  }
}
